#root {
  /*
  max-height: 800px;
  min-height: 800px;
  margin: auto;
*/
  width: 100%;
  height: 100%;

}

.home, .register {
  
  display: flex;
  flex-direction: column;
  align-items: center;
  
  width: 320px;
  height: 410px;  /* set pixels? */
  
  
  
  background-color: #1b1c20;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(20, 19, 19, 0.377);;

  
}
.home {

}


#home-links {
  width: 100%;
  height: 75%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}

#login, #register {
  width: 60%;
  height: 28%;

  background-color: rgba(0, 0, 0, 0.719);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
}

#register:hover, #login:hover, #profile-chat:hover, #profile-logout:hover {
  background-color: #595a5f6e;
}
#welcome {
  height: 25%;
  width: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: solid;
  border-color: rgba(0, 0, 0, 0.719);
  border-width: 2px;
}
#register-welcome {
  height: 20%;
  width: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: solid;
  border-width: 2px;
}
.btns {
  background-color: rgba(0, 0, 0, 0.719);
  color: white;
  border: none;
  padding: 8px 30px;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  width: 110px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.btns:hover {
  background-color: #595a5f6e;
}
.form-btns-container {
  display: flex;
  width: 100%;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  margin-top: 15%;
}
#reg-btn {
  white-space: nowrap;
}
p {
  color: white;
}

/* Profile page */
#profile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  
  width: 320px;
  height: 410px;  /* set pixels? */
  
  
  
  background-color: #1b1c20;
  border-radius: 15px;
  box-shadow: 0 0 15px rgb(80, 79, 79);
}
#profile-chat, #profile-logout, #rooms-chat {
  width: 60%;
  height: 28%;

  background-color: rgba(0, 0, 0, 0.719);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
  padding: 0;
  margin: 0;
}
#rooms-chat {
  background-color: rgba(0, 0, 0, 0.349);
  border: none;
  color: rgba(255, 255, 255, 0.486);
  opacity: 0.8;
  cursor: not-allowed;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tooltiptext-profile {
  visibility: hidden;
  position: absolute;
  z-index: 1;
  height: 85px;
  width: 192px;
  background-color: rgba(0, 0, 0, 0.349);
  border-radius: 10px;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
}

#rooms-chat:hover .tooltiptext-profile {

  visibility: visible;
}
#rooms-chat:hover {
  visibility: hidden;
}
