.border {
  border-style: inset;
  border-width: 1px;
  margin: 10px;
}

.center {
  /*
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  */
}


.login {
  border: solid 2px #777;
  width: 50%;
  background-color: aliceblue;
}

a.text {
  text-decoration: none;
  font-weight: 700;
}


#messages { 
  list-style-type: none;
  text-align: left;
  margin: 0;
  padding: 0;
  height: 20em;
  overflow: scroll;
  overflow-x: hidden;
}

#messages li { 
  padding: 5px 10px;
}

#messages li:nth-child(odd) { 
  background: #eee;
}





.register-form {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 80%;
  width: 75%;
  margin-top: -5%;
}

label {
  color: white;
  margin-bottom: 15px;
  margin-top: 15px;
}
.inputs {
  border-radius: 10px;
  text-align: center;
  color: white;
  background-color: rgb(48, 46, 46);
  width: 100%;
  height: 12.5%;
  border: none;
  margin-bottom: 10px;
  outline: none;
  font-size: 18px;
}

.inputs:focus {
  outline-style: none;
  background-color: rgb(36, 35, 35);
}









/* CHAT ROOM */

.chat-room {
  width: 100%;

  margin: auto;
  
  height: 100%;
  /*
  margin: auto; 
  /* This margin: auto solves the container moving up when resizing height */
/*
  min-height: 960px;

  min-width: 1600px;
*/

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
/*
  border: dashed;
  border-color: red;*/
}






.main-content {
  border: solid;
  border-color: #1b1c20;
  border-width: 8px;
  border-radius: 15px;
  width: 50%;
  height: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  box-shadow: 0px 0px 1px 0px rgba(0,0,0,0.7);
}

/*  Inside Main-content container */
.users-list {
  width: 20%;
  background-color: #1b1c20;
}

.messages-and-input {
  display: flex;
  flex-direction: column;
  width: 60.3%;
  height: 100%;
}

.messages {
  width: 100%;
  overflow-y: auto;
  scrollbar-width: thin;
  display: flex;
  flex-direction: column;
  height: 90%;
  background-color: rgb(20, 19, 19);

  /*box-shadow: inset 1px 1px 0px 1px rgba(0,0,0,0.7);*/
  
  -moz-box-shadow: 
  inset 0px 20px 8px -19px rgba(0,0,0,0.7),
  inset 0px -20px 8px -19px rgba(0,0,0,0.7);  
  -webkit-box-shadow: 
  inset 0px 20px 8px -19px rgba(0,0,0,0.7),
  inset 0px -20px 8px -19px rgba(0,0,0,0.7);  

  box-shadow: 
  inset 0px 20px 8px -19px rgba(0,0,0,0.7),
  inset 0px -20px 8px -19px rgba(0,0,0,0.7);
  
  align-items: center;

}
.nameAndMessage-container {
  overflow-wrap: break-word;
  
  width: 95%;
  
  margin-top: 0%;
  margin-bottom: -1.5%;
}

.name-and-time {
  display: flex;
  align-items: center;
  height: 20px;
  margin-top: 2%;
  margin-bottom: 1%;
}
.message-username {

}
.message-time {
  font-size: 11px;
  height: 40%;
}

.message-text {
  position: relative;
  padding-left: 1%;
  padding-right: 1%;
  padding-bottom: 2%;
  
  margin: 0;
}

/*
.last-space {
  border: dashed;
}
*/
/* End of main-content */

.input-container {
  height: 10%;
  position: relative;
  display: flex;
  width: 100%;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  /*background-color: #22252b;*/
  background-color: #1b1c20;
  
  border-top: solid;
  border-color: #1b1c20;
  border-width: 8px;
  
}

#input-text {
  height: 92%;
  width: 85%;
  resize: none; 
  background-color: rgb(20, 19, 19);
  border: none;
  overflow: auto;
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  color: white;

  
}

#input-btn {
  height: 100%;
  width: 15%;
  background-color: rgb(20, 19, 19);
  margin-left: 1.5%;
  color: white;
  border: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
}
#input-btn:hover {
  background-color: rgba(20, 19, 19, 0.705);
}

.future-content {
  width: 20%; /* why? */
  height: 100%;
  margin-left: 0;
  background-color: #1b1c20;

  

  display: flex;
  flex-direction: column;
  align-items: center;

}

.future-list {
  color: white;
  margin-top: -10%;
  margin-left: 10%;
}

/* End of chat room */













#users-online {
  display: flex;
  justify-content: center;
}

.each-user-container {
  display: flex;
  align-items: center;
  height: 5%;
}

.username-list {
  font-size: 16px;
  margin-left: 5%;
  width: 80%;
  overflow-wrap: break-word;
}

.green-orb {
  border: solid;
  border-color: green;
  margin-top: 3%;
  margin-left: 5%;
  height: 3px;
  width: 3px;
  background-color: green;
  border-radius: 50%;
}




input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px gray inset !important;
}


    /* FOR CHROME */
    
::-webkit-scrollbar {
      height: 12px;
      width: 12px;
      background: #000;
}
  
::-webkit-scrollbar-thumb {
      background: rgba(119, 119, 119, 0.596);
      -webkit-border-radius: 1ex;
      -webkit-box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.75);
}
  
::-webkit-scrollbar-corner {
      background: #000;
}



